@import url("https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&display=swap&subset=vietnamese");
$main_color: #51aa1b;
/* Custom font */
body,
button,
.store-mart-lite-section-footer-wrap-main h2,
input,
select,
optgroup,
textarea,
.sml-add-to-cart-wrap a.add_to_cart_button.button,
.plx_lat_prod_cat_section a.button.ajax_add_to_cart,
.plx_lat_prod_cat_section a.button.add_to_cart_button,
.plx_lat_prod_cat_section span.price,
.store-mart-lite-section-footer-wrap-main ul li a,
.main-navigation ul > li a,
.product-title,
.product-subtitle,
.top-header-left-text,
.lat-pro-title,
a.added_to_cart,
.lat-pro-subtitle,
.n2-ss-layer .n2-ow {
  font-family: "Roboto", sans-serif !important;
}
.header-two .search-form-wrap h3{
    font-size: 30px!important;
}
#pgc-211-1-0, #pgc-211-1-1 {
    width: 50%;
    width: calc(50% - ( 0.5 * 30px ) );
}
.panel-grid.panel-no-style{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
}
.panel-layout{
    #pg-211-1{
        .panel-grid-cell{
            width: 100%;
        }
    }
}
.acf-map {
	width: 100%;
	height: 400px;
	border: #ccc solid 1px;
	margin: 20px 0;
}

/* fixes potential theme css conflict */
.acf-map img {
   max-width: inherit !important;
}
@media screen and (max-width: 992px){
    .so-panel.widget.widget_sow-google-map{
        padding-left: 20px;
    }
}

.sml-add-to-cart-wrap{
    a{
        background-color: unset!important;
        color: #000!important;
        &:hover{
            color: #51aa1b!important;
        }
    }
}
input#woocommerce-product-search-field-0[type="search"]{
    width: 762px;
}
.header-two .search-form-wrap form.woocommerce-product-search input[type="search"]{
    height: 55px;
    width: 700px;
    line-height: 55px;
    border: 1px solid #e5e5e5;
    color: #8a929a;
    border-radius: 0;
    padding: 0 20px;
    font-size: 18px;
    transition: all ease 0.3s;
}
.header-two .search-form-wrap form.woocommerce-product-search button[type="submit"] {
    position: absolute;
    top: -2px;
    right: 0;
    width: 54px;
    height: 54px;
    opacity: 0;
    z-index: 1;
    cursor: pointer;
}
.header-two .search-form-wrap form.woocommerce-product-search::after{
    position: absolute;
    top: -2px;
    right: 0;
    width: 54px;
    height: 54px;
    justify-content: center;
    align-items: center;
    display: flex;
}
/* CUSTOME FOOTER */
footer{
    .store-mart-lite-container.clearfix{
        align-items: center;
    display: flex;
    justify-content: center;
    }
}
.woocommerce ul.products li.product .woocommerce-loop-product__title{
    margin-top: 10px;
}
.store-mart-lite-lat-prod-cat-wrap{
    .sml-latest-prod-add-to-cart{
        a{
            color: #222!important;
            transition: all .4s cubic-bezier(.77,0,.175,1);
            background-color: transparent!important;
            &::before{
                content: "";
                position: absolute;
                z-index: -1;
                left: 0;
                right: 100%;
                bottom: 0;
                background: #51aa1b;
                height: 2px;
                -webkit-transition-property: right;
                transition-property: right;
                -webkit-transition-duration: 0.3s;
                transition-duration: 0.3s;
                -webkit-transition-timing-function: ease-out;
                transition-timing-function: ease-out;
              
            }
            &:hover:before, &:focus:before, &:active:before {
                right: 0;
            }
        }
        
    }
}
       
.return-to-shop{
    a.button.wc-backward{
        background-color: transparent;
        color: black;
        &:hover{
            background-color: transparent;
            color: #51aa1b;
        }
    }
}
.col-3 .block.footer-widget-1{
    border-right: none!important;
}
#pg-211-1.panel-grid.panel-no-style{
    padding-top: 0px;
}
@media screen and (max-width: 768px){
    .n2-ss-layer.n2-ow{
        text-align: center!important;
        .n2-ss-item-content.n2-ow{
            font-size: 30px!important;
        }
    }
    
}
